import React, { useState } from "react";
import "./App.css";

function App() {
  const [counter, setCounter] = useState(0);
  function Plus() {
    setCounter(counter + 1);
  }
  function Minus() {
    setCounter(counter - 1);
  }
  function reset() {
    setCounter(0);
  }
  return (
    <div className="container">
      <div className="row justify-content-center">
        <div className="col-5 border">
          <div className="counter">{counter}</div>
          <div className="buttons">
            <button className="btn btn-primary b" onClick={Plus}>
              Плюс
            </button>
            <button className="btn btn-dark" onClick={Minus}>
              Минус
            </button>
          </div>
          <div className="pad">
            <a href="#" onClick={reset}>
              сброс счетчика
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
